import React from 'react';
import { inject, observer } from 'mobx-react';
import { Modal, message } from 'antd';
import Scrollbars from 'react-custom-scrollbars';
import classnames from 'classnames';

import './adjust_reason_popup.less';
message.config({
	top: 300,
	duration: 1
});
@inject('responsibleStore') @observer
class AdjustReason extends React.Component {
  constructor(props){
    super(props);
    this.state={
      inputReason:'',//
    }
  }

  handleOk=()=>{
      //
      let responsibleStore = this.props.responsibleStore;
      let addDate={};
      if(responsibleStore.removeTableInfo){
         if(responsibleStore.removeTableInfo.removeReasonID || this.state.inputReason!==''){
          addDate={
            tableID:this.props.tableID,//ID
            tableName:this.props.tableName,//
            tableCode:this.props.tableCode,//
            dutyVOList:[
              {loginID:this.props.loginID,//ID
              areaID:this.props.areaID,//ID
              mealsID:this.props.mealsID ,//ID
              areaName:this.props.areaName,//
              mealName:this.props.mealName,//
              serviceAnswerTypeList:this.props.serviceAnswerTypeList,//ID
              changeReason:responsibleStore.removeTableInfo.removeReasonID,//ID
              dealDesc:this.state.inputReason},//
            ],
        }
          responsibleStore.getRemoveTableManagerInfo(addDate);
          this.props.onOk();
        }else{
          message.warning("");
        }
      }
       
   

      // addDate={
      //     tableID:this.props.tableID,//ID
      //     tableName:this.props.tableName,//
      //     tableCode:this.props.tableCode,//
      //     dutyVOList:[
      //       {loginID:this.props.loginID,//ID
      //       areaID:this.props.areaID,//ID
      //       mealsID:this.props.mealsID ,//ID
      //       areaName:this.props.areaName,//
      //       mealName:this.props.mealName,//
      //       serviceAnswerTypeList:this.props.serviceAnswerTypeList,//ID
      //       changeReason:responsibleStore.removeTableManagerInfo.removeReasonID,//ID
      //       dealDesc:this.state.inputReason},//
      //     ],
      // }
      // if(responsibleStore.removeTableManagerInfo.removeReasonID==='' && this.state.inputReason===''){
      //   message.warning("")
      // }else{
      //   console.log('',addDate)
      //   responsibleStore.getRemoveTableManagerInfo(addDate)
      //   this.props.onOk();
      // }

  }
  handleCancel=()=>{
    this.props.onCancel();
  }
  render() {
    let responsibleStore=this.props.responsibleStore;
    let userReason;//
    userReason=responsibleStore.reasonLis.map((reason,rindex)=>{
      return <li key={rindex} className={
        classnames({
          "responsible-btn":true,
          "selected-btn":responsibleStore.currentReasonID===reason.reasonID?true:false,
        })
      } onClick={()=>{
        this.setState({inputReason:''});
        if(responsibleStore.currentReasonID!==reason.reasonID){
          responsibleStore.currentReasonID=reason.reasonID;
        }else{
          responsibleStore.currentReasonID='';
        }
        if(responsibleStore.removeTableInfo){
          responsibleStore.removeTableInfo.removeReasonID=responsibleStore.currentReasonID;//ID
        }
          
        
      

      }}>{reason.reason}
    </li>})
  
   
    return (
        <Modal title="" 
            visible={true} 
            maskClosable={false} 
            okText='' cancelText=''
            width={1100}
            wrapClassName="adjust-reason-popup-modal"
            onOk={this.handleOk}
            onCancel={this.handleCancel}
        >
         	<div className="responsible-container">
            <div className="adjust-service">
              <div className="responsible-person-box">
                <div className="adjust-reason">
                  <textarea placeholder="" onChange={(e)=>{
                    responsibleStore.currentReasonID='';
                    if(responsibleStore.removeTableInfo){
                      responsibleStore.removeTableInfo.removeReasonID='';
                    }
                    
                    let val;
                    val = e.target.value; 
                    if (val.length > 50) {  
                      val=(val.substring(0, 50));  
                      alert("50")
                    }  
                    this.setState({inputReason:val});
                    
                  }} value={this.state.inputReason}></textarea>
                  <ul>
                    {userReason}
                  </ul>
                 
                </div>
            </div> 
					</div>  
         
				</div>
      </Modal>
    )
  }
}

export default AdjustReason;


